type Store = {
  currentPage: number;
  feeds: NewsFeed[];
}

// intersection by ÌÉÄÏûÖ ÏïåÎ¶¨ÏïÑÏä§ 
type News = {
  id: number;
  time_ago: string;
  title: string;
  url: string;
  user: string;
  content: string;
}

type NewsFeed = News & {
  // id: number;
  comments_count: number;
  // url: string;
  // user: string;
  // time_ago: string;
  points: number;
  // title: string;
  read?: boolean;
}

type NewsDetail = News & {
  // id: number;
  // time_ago: string;
  // title: string;
  // url: string;
  // user: string;
  // content: string;
  comments: NewsComment[];
}

type NewsComment = News & {
  // id: number;
  // user: string;
  // time_ago: string;
  // content: string;
  comments: NewsComment[];
  level: number;
}

// ÌÉÄÏûÖ Í∞í Î™ÖÏãú
const ajax: XMLHttpRequest = new XMLHttpRequest();
const container: HTMLElement | null = document.getElementById('root');
// newsFeed
const NEWS_URL = 'http://api.hnpwa.com/v0/news/1.json'
// newsContent
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
// titleÏù¥ ÌëúÌòÑÎêòÎäî Î∂ÄÎ∂Ñ
// Í≥µÏú† Í∞í/
const store: Store = {
  currentPage: 1,
  feeds: []
};


// Îç∞Ïù¥ÌÑ∞ 

// Ïó¥Í∏∞
// ajax.open('GET', NEWS_URL , false);
// Î∂àÎü¨Ïò§Í∏∞
// ajax.send();
// Ï≤òÎ¶¨
// const newsFeed = JSON.parse(ajax.response);

// Ï§ëÎ≥µÏΩîÎìú -> Ìï®ÏàòÏïàÏóê ÎÑ£Í∏∞
// Î¶¨ÌÑ¥Í∞í NewsFeed[] | NewsDetail -> Ï†úÎÑ§Î¶≠ Í∏∞Î≤ï <>   
function getData<AjaxResponse>(url: string):AjaxResponse {
  ajax.open('GET', url, false);
  ajax.send();

  return JSON.parse(ajax.response);
}

// ÏùΩÏóàÎäîÏßÄ Ïïà ÏùΩÏóàÎäîÏßÄ Ï†ÄÏû• Ìï®Ïàò
function makeFeeds(feeds: NewsFeed[]): NewsFeed[] {
  for (let i = 0; i < feeds.length; i++){
    feeds[i].read = false;
  }

  return feeds;
}

// updateView Ìï®Ïàò
function updateView(html: string): void {
  if(container != null){
    container.innerHTML = html;
  } else{
  console.error('ÏµúÏÉÅÏúÑ Ïª®ÌÖåÏù¥ÎÑàÍ∞Ä ÏóÜÏñ¥ UIÎ•º ÏßÑÌñâÌïòÏßÄ Î™ªÌï©ÎãàÎã§.');
  }
}
// Î™©Î°ù Ìï®Ïàò
// li a 
// DOM div ÏÇ¨Ïö©Ìï¥ innerHTML ÏãúÏºú ÎßàÌÅ¨ÏóÖ Íµ¨Ï°∞Î•º ÏÑ†Î™ÖÌïòÍ≤å ÎìúÎü¨ÎÉÑ
// ulÏóê fistElementChildÏãúÏºú ÏûêÏãùÏúºÎ°ú Ìè¨Ìï®ÏãúÌÇ¥ 
// Î™©Î°ù ÌôîÎ©¥ Ìï®ÏàòÎ°ú Î¨∂Í∏∞
// const ul = document.createElement('ul');
function newsFeed(): void {
  let newsFeed: NewsFeed[] = store.feeds;
  // Î∞∞Ïó¥ 
  const newsList = [];
  // template
  let template = `
    <div class="bg-gray-600 min-h-screen">
      <div class="bg-white text-xl">
        <div class="mx-auto px-4">
          <div class="flex justify-between items-center py-6">
            <div class="flex justify-start">
              <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
              <a href="#/page/{{__prev_page__}}" class="text-gray-500">
                Previous
              </a>
              <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
                Next
              </a>
            </div>
          </div> 
        </div>
      </div>
      <div class="p-4 text-2xl text-gray-700">
        {{__news_feed__}}        
      </div>
    </div>
  `;

  // ÏµúÏ¥àÎ°ú ÌïúÎ≤à Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ 
  if (newsFeed.length === 0) {
    newsFeed = store.feeds = makeFeeds(getData<NewsFeed[]>(NEWS_URL));
  }

  // newsList.push('<ul>');
  for(let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
    // const div = document.createElement('div');
    newsList.push(`
      <div class="p-6 ${newsFeed[i].read ? 'bg-red-500' : 'bg-white'} mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
        <div class="flex">
          <div class="flex-auto">
            <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>  
          </div>
          <div class="text-center text-sm">
            <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">${newsFeed[i].comments_count}</div>
          </div>
        </div>

        <div class="flex mt-3">
          <div class="grid grid-cols-3 text-sm text-gray-500">
            <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
            <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
            <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
          </div>  
        </div>
      </div>
    `); 
    // ul.appendChild(div.firstElementChild);
  }

  // newsList.push('</ul>');
  // newsList.push(`
  //     <div>
  //         <a href = "#/page/${store.currentPage > 1 ? store.currentPage - 1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
  //         <a href = "#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
  //     </div>
  //     `);
  // container.appendChild(ul);
  // container.appendChild(content); // Îã§Î•∏ Ï∞Ω
  template = template.replace('{{__news_feed__}}', newsList.join(''));
  template = template.replace('{{__prev_page__}}', String(store.currentPage > 1 ? store.currentPage - 1 : 1));
  template = template.replace('{{__next_page__}}', String(store.currentPage + 1));

  updateView(template);
}

// ÎÇ¥Ïö© Ìï®Ïàò
// Ïù¥Î≤§Ìä∏ - hashchange
// ÎÇ¥Ïö© ÌôîÎ©¥ Ìï®Ïàò ÎπºÎÇ¥Í∏∞
function newsDetail(): void {
  // console.log('Ìï¥ÏãúÍ∞Ä Î≥ÄÍ≤ΩÎê®'); -> Í≥†Ïú†Ïùò Í∞í(id)Î•º #Ïóê Ï∂îÍ∞ÄÌï¥Ï§å
  // Îç∞Ïù¥ÌÑ∞ Ïó¥Í∏∞, Î∂àÎü¨Ïò§Í∏∞, Ï≤òÎ¶¨
  // @Ï†úÏô∏, #Ï†úÏô∏ 
  const id = location.hash.substr(7);
  // ajax.open('GET', CONTENT_URL.replace('@id', id), false);
  // ajax.send();
  // const newsContent = JSON.parse(ajax.response);
  const newsContent = getData<NewsDetail>(CONTENT_URL.replace('@id', id));
  // h1ÏúºÎ°ú ÌëúÌòÑ
  // const title = document.createElement('h1');
  // content.appendChild(title);
  // title.innerHTML = newsContent.title;
  // Î¨∏ÏûêÏó¥
  let template = `
    <div class="bg-gray-600 min-h-screen pb-8">
        <div class="bg-white text-xl">
          <div class="mx-auto px-4">
            <div class="flex justify-between items-center py-6">
              <div class="flex justify-start">
                <h1 class="font-extrabold">Hacker News</h1>
              </div>
              <div class="items-center justify-end">
                <a href="#/page/${store.currentPage}" class="text-gray-500">
                  <i class="fa fa-times"></i>
                </a>
              </div>
            </div>
          </div>
        </div>

        <div class="h-full border rounded-xl bg-white m-6 p-4 ">
          <h2>${newsContent.title}</h2>
          <div class="text-gray-400 h-20">
            ${newsContent.content}
          </div>

          {{__comments__}}

        </div>
      </div>
  `;

  // Í∏Ä ÏùΩÏùÄ Ìï®Ïàò
  for(let i = 0; i < store.feeds.length; i++){
    if(store.feeds[i].id === Number(id)) {
      store.feeds[i].read = true;
      break;
    }
  }

  updateView(template.replace('{{__comments__}}', makeComment(newsContent.comments)));
}

function makeComment(comments: NewsComment[]): string {
  const commentString = [];

  for(let i = 0; i < comments.length; i++) {
    const comment: NewsComment = comments[i];

    commentString.push(`
      <div style="padding-left: ${comment.level * 40}px;" class="mt-4">
        <div class="text-gray-400">
          <i class="fa fa-sort-up mr-2"></i>
          <strong>${comment.user}</strong> ${comment.time_ago}
        </div>

        <p class="text-gray-700">${comment.content}</p>
      </div>
    `);

    if(comments[i].comments.length > 0) {
      commentString.push(makeComment(comment.comments));
    }
  }

  return commentString.join('');
}

  // ÎùºÏö∞ÌÑ∞ 
function router(): void {
  const routePath = location.hash;

  if(routePath ===''){
    newsFeed(); // Î™©Î°ù Ìò∏Ï∂ú
  }else if(routePath.indexOf('#/page/') >= 0){
    store.currentPage = Number(routePath.substr(7));
    newsFeed(); // ÌéòÏù¥Ïßï, Î™©Î°ù Ìò∏Ï∂ú
  }else {
    newsDetail(); // ÎÇ¥Ïö© Ìò∏Ï∂ú
  }
}

window.addEventListener('hashchange', router);

router();